<template>
  <div id="common-box">
    <div class="common-form">
        <div class="myinfo">
            <h4>当前登录用户信息</h4>
            <el-form :model="ruleForm"
                ref="ruleForm" label-width="150px" class="demo-ruleForm">
                <div class="form-left">
                    <el-form-item label="用户昵称" prop="realName">
                        {{ ruleForm.nickName }}
                    </el-form-item>
                    <el-form-item label="电子邮箱">
                        {{ ruleForm.userEmail }}
                    </el-form-item>
                    <el-form-item label="手机号码">
                        {{ ruleForm.userPhone }}
                    </el-form-item>
                </div>
            </el-form>  
        </div>      
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      ruleForm:{}
    }
  },
  methods:{
    getUserInfo(){
        let that=this;
        that.$api.userinfo().then(res=>{
            that.ruleForm=res.data.user;
        })
    }
  },
  created(){
      this.getUserInfo();
  }
}
</script>

<style lang="scss" scoped>

    .myinfo{
        border: 1px solid #1f499f;
        padding: 6px;
    }
    .upload-img{
        width:80px;
        height:100px;
        background: #f8f8f8;
        border: 1px solid #eee;
    }
</style>